body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato' , sans-serif;
  color: #333;
  background-size: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
  background-color: #475264;
}

p {
  margin: 0;
  padding: 0 0 10px 0;
  line-height: 20px;
}

.span4 {
  width: 80px;
  float: left;
  margin: 0 8px 10px 8px;
}

.phone {
  margin-top: 15px;
  background: #fff;
}

.tel {
  margin-bottom: 10px;
  margin-top: 0;
  border: 1px solid #9e9e9e;
  border-radius: 0px;
}

.num-pad {
  padding-left: 15px;
  padding-top: 20px;
}

.num {
  border: 1px solid #9e9e9e;
  border-radius: 999px;
  -moz-border-radius: 999px;
  height: 80px;
  background-color: #fff;
  color: #333;
  cursor: pointer;
}

.num:hover {
  background-color: #9e9e9e;
  color: #fff;
  transition-property: background-color .2s linear 0s;
  -moz-transition: background-color .2s linear 0s;
  -webkit-transition: background-color .2s linear 0s;
  -o-transition: background-color .2s linear 0s;
}

.txt {
  font-size: 25px;
  text-align: center;
  margin-top: 15px;
  font-family: 'Lato' , sans-serif;
  line-height: 30px;
  color: #333;
}

.small {
  font-size: 10px;
}

.btn {
  font-weight: bold;
  -webkit-transition: .1s ease-in background-color;
  -webkit-font-smoothing: antialiased;
  letter-spacing: 1px;
}

.btn:hover {
  transition-property: background-color .2s linear 0s;
  -moz-transition: background-color .2s linear 0s;
  -webkit-transition: background-color .2s linear 0s;
  -o-transition: background-color .2s linear 0s;
}

.spanicons {
  width: 72px;
  float: left;
  text-align: center;
  margin-top: 40px;
  color: #9e9e9e;
  font-size: 30px;
  cursor: pointer;
}

.spanicons:hover {
  color: #3498db;
  transition-property: color .2s linear 0s;
  -moz-transition: color .2s linear 0s;
  -webkit-transition: color .2s linear 0s;
  -o-transition: color .2s linear 0s;
}

.active {
  color: #3498db;
}

.clearfix {
  padding-bottom: 20px;
}

.footer {

  position: absolute;
  width: 100%;
  bottom: 0;
  overflow:hidden;
  padding: 1rem 1.5rem 1rem;

}

#callButton {
  width: 90%;
}

#hangupButton {
  width: 90%;
}

#telNumber {
  width: 90%;
}

#hoplon {
  border-radius: 50%;
  border: 2px solid #D9D9D9;
}

#ares-title {
  font-family: 'Lato', sans-serif; 
  font-size: 1.75rem;
}

#ares-dropdown, #ares-dropdown1, #ares-dropdown2, #ares-dropdown3, #ares-dropdown4, #ares-dropdown5, #ares-dropdown6 {
  color: black; 
  background-color: transparent;
}

#ares-overlay {
  display: -webkit-flex;
  display: flex; 
  -webkit-flex-direction: column; 
          flex-direction: column; 
  -webkit-align-items: center; 
          align-items: center; 
  -webkit-justify-content: center; 
          justify-content: center;
}

#ares-video {
  opacity: 0.30; 
  height: 70vh;
}

#home-shortcut, #home-icon {
  color: white;
}
